*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

html {
  background-color: #212121;
}

.navbar {
  background-color: #111;
  height: 2.5rem;
  display: flex;
  justify-content: center;
}

.nav-links {
  background: none;
  border: none;
  color: #fbfbfb;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  flex-basis: 6rem;
  border-radius: 0.5rem;
}

.desktop-nav-links {
  display: none;
}

.nav-links:focus {
  background-color: #000;
  outline: none;
}

@media (min-width: 848px) {
  .desktop-nav-links {
    display: block;
    flex: 0 0 25%;
  }

  .nav-links {
    flex: 0 0 25%;
  }

  .navbar {
    justify-content: space-around;
  }

  .nav-links:hover {
    background-color: #000;
    outline: none;
    cursor: pointer;
  }
}

.hero {
  background-image: url(/static/media/mobile-hero.d203cdab.jpeg);
  background-size: cover;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-title {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  color: #fbfbfb;
  font-size: 2.12rem;
  margin-top: 2.2rem;
}

.hero-subtitle {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #fbfbfb;
  font-size: 1rem;
  margin-bottom: 2rem;
}

@media (min-width: 848px) {
  .hero {
    background-image: url(/static/media/desktop-hero.7f491eaa.jpeg);
    background-size: cover;
    height: 95vh;
  }

  .hero-title {
    font-size: 6rem;
    margin-top: 4rem;
  }

  .hero-subtitle {
    font-size: 3rem;
  }
}

.skills {
  background-color: #c12d2d;
  color: #fbfbfb;
}

.skills-heading {
  font-family: 'Rubik', sans-serif;
  font-size: 2.12rem;
  font-weight: 300;
  width: 100%;
  text-align: center;
}

.skills-heading::after {
  content: '';
  display: block;
  height: 0.2rem;
  width: 100%;
  background-color: #212121;
  margin-bottom: 1.3rem;
}

.skills-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.list-heading {
  font-family: 'Roboto', sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 0.2em;
  text-align: center;
}

.skill-list {
  list-style: none;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  margin-bottom: 2em;
}

@media (min-width: 848px) {
  .skills {
    height: 80vh;
  }

  .skills-heading {
    font-size: 3.75rem;
  }

  .skills-container {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    margin-top: 6rem;
  }

  .list-container {
    flex-basis: 33%;
  }

  .list-heading {
    font-size: 2.12rem;
  }

  .skill-list {
    font-size: 1.25rem;
  }
}

.projects {
  /* background-color: rgb(31, 31, 31); */
}

.projects-heading {
  font-family: 'Roboto', sans-serif;
  font-size: 2.12rem;
  font-weight: 300;
  color: #fbfbfb;
  text-align: center;
  margin: 0.5rem 0 1rem 0;
}

.project-card {
  background-size: cover;
  height: 280px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-one-card {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/clientpanel.d5c0f99a.png);
}

.project-two-card {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/contactmanager.3c6c33bb.png);
}

.project-three-card {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/microblogger.61ab18f3.png);
}

.project-link {
  text-decoration: none;
}

.project-info {
  font-family: 'Roboto', sans-serif;
  font-size: 2.12rem;
  color: #fbfbfb;
  text-align: center;
}

.source {
  display: block;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  color: #7fa9bf;
  text-align: center;
  width: 6rem;
  margin: 0.5rem auto 1rem auto;
}

.source::before {
  content: '';
  background-image: url(/static/media/github.9ac279f9.png);
  background-size: 1rem;
  display: inline-block;
  height: 1rem;
  width: 1rem;
  position: relative;
  right: 0.4rem;
  top: 0.1rem;
}

.desktop-project-info {
  display: none;
}

.desktop-project-headline {
  display: none;
}

@media (min-width: 848px) {
  .projects-heading {
    font-size: 3.75rem;
  }

  .projects-container {
    display: flex;
    justify-content: space-around;
  }

  .project-one-card {
    background-image: url(/static/media/clientpanel.d5c0f99a.png);
  }

  .project-two-card {
    background-image: url(/static/media/contactmanager.3c6c33bb.png);
  }

  .project-three-card {
    background-image: url(/static/media/microblogger.61ab18f3.png);
  }

  .desktop-project-container {
    flex-basis: 30%;
  }

  .desktop-project-headline {
    display: block;
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    color: #fbfbfb;
  }

  .desktop-project-info {
    display: block;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    font-size: 1.25rem;
    font-weight: 300;
    color: #fbfbfb;
  }

  .project-info {
    display: none;
  }
}

.footer {
  background-color: #111;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 8.5rem;
}

.contact-heading {
  font-family: 'Rubik', sans-serif;
  font-size: 2.12rem;
  font-weight: 400;
  color: #fbfbfb;
}

.contact-info {
  font-family: 'Roboto', sans-serif;
  font-size: 1.25rem;
  color: #fbfbfb;
}

.desktop-contact-links {
  text-decoration: none;
  color: #7fa9bf;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
}

.fab {
  color: #fbfbfb;
  margin: 0 0.5rem 0 0.5rem;
}

.desktop-contact-container {
  display: none;
}

@media (min-width: 848px) {
  .desktop-contact-container {
    display: block;
  }

  .contact-info,
  .contact-heading {
    display: none;
  }

  .desktop-contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .desktop-contact-heading {
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 2.12rem;
    font-weight: 300;
    color: #fbfbfb;
  }

  .desktop-contact-info {
    font-family: 'Roboto', sans-serif;
    font-size: 1.25rem;
    color: #fbfbfb;
    text-align: center;
  }

  .desktop-contact-button {
    border: none;
    border-radius: 0.5rem;
    height: 1.87rem;
    width: 9rem;
    background-color: #c12d2d;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    color: #fbfbfb;
    text-decoration: none;
    text-align: center;
    line-height: 1.87rem;
  }
}

